
import { useEffect, useRef } from 'react';
import type { Plan, Phase, Language, PlanScheduleEntry } from './types';
import { I18N } from './constants';

export type AnalyzerStructuredInputs = {
  problemLocation: string;
  problemSpread: string;
  recentChanges: string;
};

export const buildAnalyzerNotes = (
  userNotes: string,
  structuredInputs: AnalyzerStructuredInputs
): string => {
  const parts: string[] = [];
  if (userNotes.trim()) {
    parts.push(userNotes.trim());
  }

  const structuredParts: string[] = [];
  if (structuredInputs.problemLocation) {
    structuredParts.push(`Ort des Problems: ${structuredInputs.problemLocation}`);
  }
  if (structuredInputs.problemSpread) {
    structuredParts.push(`Ausbreitungsgeschwindigkeit: ${structuredInputs.problemSpread}`);
  }
  if (structuredInputs.recentChanges) {
    structuredParts.push(`Kürzliche Änderungen: ${structuredInputs.recentChanges}`);
  }

  if (structuredParts.length > 0) {
    parts.push('Zusätzliche Beobachtungen:\n- ' + structuredParts.join('\n- '));
  }

  return parts.join('\n\n');
};

export const getPhaseTag = (phase: Phase, lang: Language): string => {
  const weekNumber = getWeekNumber(phase);
  if (weekNumber === null) {
    return lang === 'de' ? 'Veg' : 'Veg';
  }
  if (weekNumber >= 9) {
    return lang === 'de' ? 'Reife' : 'Ripen';
  }
  return lang === 'de' ? 'Blüte' : 'Flower';
};

export const getWeekNumber = (phase: Phase): number | null => {
  const match = phase.match(/^W(\d+)$/);
  return match ? parseInt(match[1], 10) : null;
};

export const sortPhases = (phases: Phase[]): Phase[] => {
  return phases.sort((a, b) => {
    const aIsVeg = getWeekNumber(a) === null;
    const bIsVeg = getWeekNumber(b) === null;

    if (aIsVeg && !bIsVeg) return -1;
    if (!aIsVeg && bIsVeg) return 1;
    if (aIsVeg && bIsVeg) return a.localeCompare(b);

    const aWeek = getWeekNumber(a) ?? 0;
    const bWeek = getWeekNumber(b) ?? 0;
    return aWeek - bWeek;
  });
};

export const getStageInfo = (phase: Phase): { class: 'VEG' | 'FLOWER' | 'RIPEN'; color: string; IconComponent: React.FC<React.SVGProps<SVGSVGElement>>; } => {
    const weekNumber = getWeekNumber(phase);
    const { Leaf, Flower, Ripen } = require('./components/icons'); 
    if (weekNumber === null) {
      return { class: 'VEG', color: '#4ade80', IconComponent: Leaf };
    }
    if (weekNumber >= 9) {
      return { class: 'RIPEN', color: '#facc15', IconComponent: Ripen };
    }
    return { class: 'FLOWER', color: '#f87171', IconComponent: Flower };
};

export const resizeImage = (file: File, maxWidth: number): Promise<string> => {
    return new Promise((resolve, reject) => {
        const img = new Image();
        img.src = URL.createObjectURL(file);
        img.onload = () => {
            const canvas = document.createElement('canvas');
            const scale = maxWidth / img.width;
            canvas.width = maxWidth;
            canvas.height = img.height * scale;
            const ctx = canvas.getContext('2d');
            if (!ctx) {
                return reject(new Error('Failed to get canvas context'));
            }
            ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
            resolve(canvas.toDataURL('image/jpeg'));
        };
        img.onerror = (error) => reject(error);
    });
};

export const computePlanSchedule = (plan: Plan, startDate: string, weekStartsOn: 0 | 1 | 2 | 3 | 4 | 5 | 6): PlanScheduleEntry[] => {
    if (!startDate) return [];

    const start = new Date(startDate + 'T00:00:00.000Z');
    let current = new Date(start.getTime());

    // Adjust to the selected week start day
    const dayOfWeek = current.getUTCDay();
    const diff = (dayOfWeek - weekStartsOn + 7) % 7;
    current.setUTCDate(current.getUTCDate() - diff);

    const schedule: PlanScheduleEntry[] = [];
    for (const entry of plan) {
        const duration = entry.durationDays ?? 7;
        const end = new Date(current.getTime());
        end.setUTCDate(end.getUTCDate() + duration -1);
        schedule.push({
            phase: entry.phase,
            start: new Date(current.getTime()),
            end,
        });
        current.setUTCDate(current.getUTCDate() + duration);
    }
    return schedule;
};


export const toLocalDateFromUTC = (utcDate: Date): Date => {
  return new Date(utcDate.getUTCFullYear(), utcDate.getUTCMonth(), utcDate.getUTCDate());
}

export const findScheduleIndexForDate = (schedule: PlanScheduleEntry[], date: Date): number => {
    return schedule.findIndex(item => date >= item.start && date <= item.end);
};

export const generateClientId = (prefix: string = 'id'): string => {
  return `${prefix}-${Date.now()}-${Math.random().toString(36).substring(2, 8)}`;
};

export function useDebouncedEffect(effect: () => void, deps: any[], delay: number) {
  const handler = useRef<number | undefined>();
  
  useEffect(() => {
    handler.current = setTimeout(() => {
      effect();
    }, delay);

    return () => {
      clearTimeout(handler.current);
    };
  }, [...(deps || []), delay, effect]);
}
