import type { Cultivar, Substrate, JournalEntry } from '../types';


async function backendAvailable(): Promise<boolean> {
  try {
    const res = await fetch("api/health");
    return res.ok;
  } catch {
    return false;
  }
}

async function backendLoad(collection: string, key: string): Promise<any | null> {
  try {
    const res = await fetch(`api/store/${collection}/${encodeURIComponent(key)}`);
    if (!res.ok) return null;
    const json = await res.json();
    return json?.data ?? null;
  } catch {
    return null;
  }
}

async function backendSave(collection: string, key: string, data: any): Promise<void> {
  try {
    await fetch(`api/store/${collection}/${encodeURIComponent(key)}`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ data }),
    });
  } catch (error) {
    console.error('Failed to persist journal data to backend', error);
  }
}

type JournalCache = Record<string, JournalEntry[]>;
type GrowListCache = Record<string, string[]>;
type ActiveGrowCache = Record<string, string | undefined>;
type BackendStatus = 'unknown' | 'available' | 'unavailable';

const BACKEND_COLLECTION = 'photonfluxJournal';
const BACKEND_GROW_LIST_KEY_PREFIX = 'growList';
const BACKEND_ACTIVE_GROW_KEY_PREFIX = 'activeGrow';

const journalCache: JournalCache = {};
const growListCache: GrowListCache = {};
const activeGrowCache: ActiveGrowCache = {};

const journalSubscribers = new Map<string, Set<() => void>>();
const growListSubscribers = new Map<string, Set<() => void>>();
const syncPromises = new Map<string, Promise<void>>();

let backendStatus: BackendStatus = 'unknown';

const ensureBackendStatus = async (): Promise<boolean> => {
  if (backendStatus === 'available') return true;
  if (backendStatus === 'unavailable') return false;
  const available = await backendAvailable();
  backendStatus = available ? 'available' : 'unavailable';
  return available;
};

const notifySubscribers = (key: string) => {
  const listeners = journalSubscribers.get(key);
  if (!listeners) return;
  listeners.forEach(listener => {
    try {
      listener();
    } catch (error) {
      console.error('Journal subscriber callback failed', error);
    }
  });
};

const persistToBackend = (key: string, data: any): void => {
  ensureBackendStatus()
    .then(available => {
      if (!available) return;
      return backendSave(BACKEND_COLLECTION, key, data);
    })
    .catch(error => console.error(`Failed to persist data for key ${key}`, error));
};

const getGrowListKey = (cultivar: Cultivar, substrate: Substrate) =>
  `${BACKEND_GROW_LIST_KEY_PREFIX}_${cultivar}_${substrate}`;

const getActiveGrowKey = (cultivar: Cultivar, substrate: Substrate) =>
  `${BACKEND_ACTIVE_GROW_KEY_PREFIX}_${cultivar}_${substrate}`;

const notifyGrowListSubscribers = (cultivar: Cultivar, substrate: Substrate) => {
  const key = getGrowListKey(cultivar, substrate);
  growListSubscribers.get(key)?.forEach(cb => cb());
};

export const getGrowList = (cultivar: Cultivar, substrate: Substrate): string[] => {
  const key = getGrowListKey(cultivar, substrate);
  if (!growListCache[key]) {
     // Immediately sync this, as it's needed for the UI to render grows.
    void (async () => {
      const data = await backendLoad(BACKEND_COLLECTION, key);
      growListCache[key] = Array.isArray(data) ? data : [];
      notifyGrowListSubscribers(cultivar, substrate);
    })();
  }
  return growListCache[key] || [];
};

export const getActiveGrow = (cultivar: Cultivar, substrate: Substrate): string | undefined => {
  const key = getActiveGrowKey(cultivar, substrate);
  if (activeGrowCache[key] === undefined) {
    // Sync in background
     void (async () => {
      const data = await backendLoad(BACKEND_COLLECTION, key);
      activeGrowCache[key] = typeof data === 'string' ? data : undefined;
      notifyGrowListSubscribers(cultivar, substrate); // Notify to re-render active state
    })();
  }
  return activeGrowCache[key];
};

export const setActiveGrow = (cultivar: Cultivar, substrate: Substrate, startDate: string) => {
  const listKey = getGrowListKey(cultivar, substrate);
  const activeKey = getActiveGrowKey(cultivar, substrate);

  const currentList = growListCache[listKey] || [];
  if (!currentList.includes(startDate)) {
    const newList = [...currentList, startDate].sort((a,b) => new Date(b).getTime() - new Date(a).getTime());
    growListCache[listKey] = newList;
    persistToBackend(listKey, newList);
  }

  activeGrowCache[activeKey] = startDate;
  persistToBackend(activeKey, startDate);
  notifyGrowListSubscribers(cultivar, substrate);
};

export const addNewGrow = (cultivar: Cultivar, substrate: Substrate, startDate: string) => {
    setActiveGrow(cultivar, substrate, startDate);
};

export const subscribeToGrowList = (
  cultivar: Cultivar,
  substrate: Substrate,
  listener: () => void,
): (() => void) => {
  const key = getGrowListKey(cultivar, substrate);
  const listeners = growListSubscribers.get(key) ?? new Set<() => void>();
  listeners.add(listener);
  growListSubscribers.set(key, listeners);
  // Initial load
  getGrowList(cultivar, substrate);
  getActiveGrow(cultivar, substrate);

  return () => {
    growListSubscribers.get(key)?.delete(listener);
  };
};

const ensureJournalSynced = async (key: string): Promise<void> => {
  if (backendStatus === 'unavailable') return;
  if (syncPromises.has(key)) return syncPromises.get(key)!;

  const promise = (async () => {
    const available = await ensureBackendStatus();
    if (!available) return;

    try {
      const backendData = await backendLoad(BACKEND_COLLECTION, key);
      if (Array.isArray(backendData)) {
        const serialized = JSON.stringify(journalCache[key] ?? []);
        const incoming = JSON.stringify(backendData);
        if (serialized !== incoming) {
          journalCache[key] = backendData as JournalEntry[];
          notifySubscribers(key);
        }
      }
    } catch (error) {
      console.error('Failed to synchronise journal data', error);
    }
  })();

  syncPromises.set(key, promise);
  promise.finally(() => syncPromises.delete(key));
  return promise;
};

const getJournalKey = (cultivar: Cultivar, substrate: Substrate, startDate: string | undefined): string => {
    return `growJournalV2_${cultivar}_${substrate}_${startDate}`;
};

export const loadJournal = (cultivar: Cultivar, substrate: Substrate, startDate: string | undefined): JournalEntry[] => {

    const key = getJournalKey(cultivar, substrate, startDate);
    if (!journalCache[key]) {
        journalCache[key] = [];
    }
    void ensureJournalSynced(key);
    const entries = journalCache[key] ?? [];
    return [...entries].sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
};

export const saveJournal = (cultivar: Cultivar, substrate: Substrate, startDate: string | undefined, entries: JournalEntry[]): void => {

    const key = getJournalKey(cultivar, substrate, startDate);
    journalCache[key] = entries;
    persistToBackend(key, entries);
    notifySubscribers(key);
};

export const subscribe = (
    cultivar: Cultivar,
    substrate: Substrate,
    startDate: string | undefined,
    listener: () => void,
): (() => void) => {
    const key = getJournalKey(cultivar, substrate, startDate);
    const listeners = journalSubscribers.get(key) ?? new Set<() => void>();
    listeners.add(listener);
    journalSubscribers.set(key, listeners);
    void ensureJournalSynced(key);
    return () => {
        const bucket = journalSubscribers.get(key);
        if (!bucket) return;
        bucket.delete(listener);
        if (bucket.size === 0) {
            journalSubscribers.delete(key);
        }
    };
};